

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;

}
body {
  
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: medium;
  color: rgb(2, 2, 59);

}

a {
  color:rgb(12, 233, 134);
}


a:hover {
  color:rgb(221, 255, 0);
  text-decoration: none;
  transform: scale(1.15); 
}


header {
  width: 100%;
  position:relative;

}
h3 span {
text-align: right;
}


.navigation {

  top: 0;
  left: -100%;
  width: 100%;
  width: 100vh;
  display: flex;
  background:white;
  justify-content: center;
  align-items: center;
  transition: 1s;
  font-size: large;
}
.banner {
  position: relative;
  width: 100%;
  height: 100%;
  color: cadetblue;
  font-family: cursive;

}
.cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.navLink {
  position: relative;
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  font-weight: bold;
}

.navLink ul {
  position: relative;
  width: 100%;
  font-weight: bold;
}
.navLink ul li {
  position: relative;
  list-style: none;
  font-weight: bold;
  

}

.navLink ul li a {
  position: relative;
  color:rgb(19, 235, 109);
  font-size: 2em;
  font-weight: 400;
  text-decoration: none;
  margin:5px 0;
  margin-left: 100p;
  display: inline-block;
  letter-spacing: 1px;
  text-transform: uppercase;


}

.navbar {
  background-color: fuchsia;
}

ul {
  font-size: medium;
}

.card-text {
  font-size: small;
}


.navLink ul li a:hover {
  color: yellow;
}

.lead mb-0 a:hover {
  color: white;
  font-weight: bolder;

}
.lead mb-0 a {
  color:blue;
  font-weight: bolder;

}

h2 {
text-align: left;
font-family:inherit;
color:rgb(25, 31, 112)
}

h3 {
font-family: cursive;
color: rgb(12, 233, 134);

}
h4 {
font-weight: bolder;
color: rgb(2, 2, 59);
font-family: cursive;

}

h5 {
text-align: center;
padding-right: 5px;
font-family: cursive;
}
.lead mb-0 {
font-size: smaller;
}


h6 {
font-family: cursive;
color: rgb(2, 2, 59);
font-size: medium;
}
container {
  display: flex;
}
.card {
background-color: cadetblue;
}
.card2 {
  background-color: rgb(76, 102, 155);
  color:fuchsia;
}

.card-text {
  color:white;
}

.btn btn-primary {
background-color: chartreuse;
border-color: ch;

}

.list-inline mb-2 {
color:blueviolet;
}


.columns {
  justify-content: center;
  padding-top: 20px;
  padding-left: 100px;
}

.flexbox {
align-items: center;
justify-items: center;
padding-left: 100px;
}

.rotate {

transform: rotate(45deg);
background-color: #8ac4ff;
}

.zoomin {
width: 150px;
height: 150%;
overflow: hidden;
margin: auto;
}
.zoomin img {
width: 100%;
transition: 0.5s all ease-in-out;

}

.zoomin:hover img {
transform: scale(1.5);
}

footer {
  padding: 20px;
  text-align: center;
  align-items: center;
  background: white;
  color: cadetblue;
  font-family: cursive;
  font-size:xx-large;
  font-weight: bolder;
  justify-content: center;
}

.form {
  margin: 100px;
}

.form input {
  display: block;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 50px;
}


body {
  
  background-color: white;
  color: rgb(2, 2, 59);
  
}

.col-md-4 mb-5 {
  align-content: center;
  justify-items: center;
}

.App {
  text-align: center;
}

#light .switch2 {
  background-color: white;
  color: rgb(2, 2, 59);
}

#dark .switch2 {
  background-color: black;
  color:white;
}

#light header {
  background-color: white;
}

#dark header {
  background-color: black;
}

#light body {
  background-color: white;
  color: rgb(2, 2, 59);
}

#dark body {
  background-color: black;
  color:white
}

#light footer {
  background-color: white;
}
.nps h6 {
  color:#055aee
}

#dark footer {
  background-color: black;
  color: white
}

#dark h6 {
  color: white
}

#dark h4 {
  color: white

}

#dark h3 {
  color:fuchsia;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.switch {
  display: flex;
  width: 100vw;
  height: 100px;
  justify-content: center;
  align-items: center;
  

}

.switch label {
  margin: 10px;
}

#light .switch label {
  color:rgb(10, 10, 228);
}

#dark .switch label {
  color: white;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: rgb(2, 2, 59);

}


.zoom:hover {
  transform: scale(1.08); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

